.c-wrapper {
    width: 100%;
    padding: 20px 0; /* Redujo el padding para hacer más compacto el contenido */
    background-color: #000000;
    color: rgb(29, 28, 28);
}

.c-container {
    display: flex;
    justify-content: space-between; /* Espacio entre el contenido de la izquierda y derecha */
    align-items: center; /* Alinea verticalmente al centro */
    gap: 1rem; /* Reduce el espacio entre los elementos */
    margin: 0 9rem; /* Márgenes laterales más pequeños */
}

.c-left {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    max-width: 600px; /* Ajusta el ancho máximo */
    width: 100%;
}

.contactModes .row {
    display: flex;
    flex-wrap: wrap; /* Permite que los cuadros se distribuyan en filas */
    gap: 1rem; /* Reduce el espacio entre los cuadros */
}

.mode {
    background-color: #f0f0f0;
    padding: 1rem;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease;
    flex: 1 1 calc(50% - 0.5rem); /* Ocupa el 50% del ancho, con menos espacio entre los cuadros */
    box-sizing: border-box;
}

.mode:hover {
    transform: translateY(-5px);
}

.icon {
    margin-right: 10px;
    color: #3549ff;
}

.detail {
    display: flex;
    flex-direction: column;
}

.button {
    margin-top: 1rem;
    padding: 0.5rem 1rem;
    background-color: #1d2adf;
    color: white;
    border-radius: 5px;
    cursor: pointer;
    text-align: center;
}

.orangeText {
    color: #3549ff;
}

.primaryText {
    font-size: 26px;
    font-weight: bold;
    color: #dbdbdb;
}

.secondaryText {
    font-size: 18px;
    color: #aaa;
}

.c-right {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 1rem; /* Reduce el margen en el lado izquierdo del logo */
}

.logo-image {
    max-width: 400px; /* Aumenta ligeramente el tamaño máximo del logo */
    width: 100%;
    height: auto;
}

/* Media query para pantallas pequeñas */
@media (max-width: 768px) {
    .c-container {
        flex-direction: column; /* Cambia la dirección de los elementos a columna */
        align-items: center; /* Centra los elementos en pantallas pequeñas */
        gap: 2rem; /* Espacio mayor entre secciones en pantallas pequeñas */
    }

    .c-left {
        max-width: 100%;
        align-items: center; /* Centra el texto en pantallas pequeñas */
    }

    .contactModes .row {
        flex-direction: column; /* Los cuadros se apilan uno sobre otro en pantallas pequeñas */
        gap: 1rem; /* Espacio entre cuadros */
    }

    .logo-image {
        max-width: 150px; /* Reduce el tamaño del logo en pantallas pequeñas */
    }
}
